html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.card {
  box-sizing: border-box;
  color: #060b35;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__img {
  width: 160px;
  height: 134px;
  margin-inline: auto;
  display: block;
}

.card__title, .card__price-text {
  font-size: 12px;
  line-height: 18px;
}

.card__title {
  margin: 0;
  margin-top: 40px;
  font-weight: 500;
}

.card__subtitle, .card__reviews-text {
  font-size: 10px;
  line-height: 14px;
}

.card__subtitle {
  color: #616070;
  margin: 4px 0 0;
  font-weight: 400;
}

.card__reviews {
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 16px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  display: flex;
}

.card__price-text {
  color: #616070;
}

.card__price-num {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  box-sizing: border-box;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  width: fit-content;
  display: flex;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:nth-child(n+2) {
  margin-left: 4px;
}
